@import "vars";

@media only screen and (max-width : 1200px) {
  .promotion-slider__type1 {
    .promotion__content-btn {
      margin-top: 67px; }

    .promotion__content-title {
      margin-bottom: 35px; } }

  .promotion__content-title {
    font-size: 43px;
    line-height: 52px; }


  .promotion__content-text {
    margin-left: 0;
    font-size: 26px;
    line-height: 32px; }

  .middle-line {
    padding: 25px 15px 25px; }

  .middle-line__wrapper-left {
    width: 67%; }

  .middle-line__wrapper-right {
    display: block;
    text-align: center; }

  //.middle-line__wrapper-right
  //  display: none


  .middle-line__email {
    display: block;
    margin-bottom: 10px;
    margin-right: 0; }

  .middle-line__tel {
    display: block;
    margin-bottom: 10px;
    margin-right: 0; }

  .middle-line__telegram {
    margin-left: 35px; }

  .top-line {
    padding: 30px 0; }

  .top-line__menu {
    display: none; }

  .top-line__tools {
    display: none; }
  .burger {
    display: block; }

  .right-menu {
    display: block; }

  .right-menu__active {
    transform: translateX(0%); }

  .bottom-line__list {
    display: none; }

  .mobile {
    padding-top: 50px; }

  .mobile-content {
    margin: 0 auto; }

  .mobile-content__wrapper {
    text-align: center; }

  .mobile-content__image {
    display: none; }

  .mobile-content__link {
    display: inline-flex; }

  //.our-clients__items-logo
  //  width: calc(100%/4 - 17px)

  .mobile-content__text {
    max-width: 100%; }

  .mob__link {
    justify-content: center; } }


@media only screen and (max-width : 991px) {
  body {
    overflow-x: hidden; }

  .middle-line {
    justify-content: space-around;
    padding: 15px 15px 15px; }

  .middle-line__wrapper-left {
    width: 50%;
    padding-bottom: 40px; }

  .top-line__right {
    margin-right: 40px; }

  .middle-line__wrapper-right {
    margin-top: 30px; }


  .middle-line__logo {
    width: 90%;
 }    //margin: 0

  .middle-line__promotion {
    display: none; }

  .options-connect__checkbox-wrapper {
    display: block;
    max-width: 265px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    text-align: left; }

  .options-connect__switch {
    margin-bottom: 25px; }
  .options-connect__select-checkbox {
    margin: 0 auto; }
  .offer__check-title {
    font-size: 40px; }

  .our-clients__items-logo {
    margin: 6px; }

  .wrapper {
    padding: 0; }



  .news-slider__item {
    width: 100%;
    margin: 10px; }

  .footer-content {
    display: none; }

  .footer__accordion-wrapper {
    display: flex;
    justify-content: space-between; }

  .footer-link {
    justify-content: space-around; }

  .link_tp {
    padding-top: 40px; }

  .middle__title {
    font-size: 43px; }
  .HowConnect-content__title {
    font-size: 43px; }
  .connection-check__form-inner {
    display: block;
    text-align: center; }

  .connection-check__form-input {
    display: block;
    margin: 0 auto 20px; }

  .connection-check__form-btn {
    margin-left: 0; }

  .info__title {
    font-size: 43px; }

  .info__text {
    padding: 0 20px; }

  .services-card {
    margin: 0 auto; }
  .services-card__item {
    width: calc(100%/2 - 20px); }

  .application-inner__wrapper {
    display: block; }

  .application-capability {
    justify-content: center; }

  .capability-img {
    width: 36%; }
  .application-about {
    display: flex;
    justify-content: space-around;
    max-width: 100%;
    padding-left: 0;
    padding-top: 30px; }

  .about-text {
    max-width: 390px; }
  .about-link {
    position: static;
    display: block;
    margin: auto 0;
    padding-right: 6%;
    .btn__link {
      margin-left: 0;

      & + .btn__link {
        margin-top: 10px; } } }

  .HowConnect-content__list {
    padding-left: 20px; }

  .HowConnect-feedback__title {
    padding-left: 0; }

  .feedback-link {
    display: block;
    margin-top: 10px; } }
@media only screen and (max-width : 480px) {


  .middle-line {
    display: block;
    text-align: center; }

  .middle-line__wrapper-left {
    width: 100%;
    display: block;
    padding-bottom: 0; }

  .middle-line__promotion {
    display: block;
    margin-top: 20px;
    padding-left: 0;
    &:before {
      display: none; } }
  .middle-line__logo {
    display: block;
    margin: 0 auto; }

  .middle-line__email {
    margin-right: 0; }

  .middle-line__tel {
    margin-right: 0; }

  .middle-line__telegram {
    margin-left: 0; }

  .right-menu {
    width: 100%;
    border-radius: 0; }

  .promotion-slider__type1 {
    .promotion__content-title {
      margin-bottom: 37px; }
    .promotion__content-btn {
      margin-top: 44px; } }

  .promotion__content-title {
    font-size: 32px;
    line-height: 39px; }



  .options__item {
    padding-left: 5px;
    padding-right: 5px; }
  .router-content {
    margin-bottom: 70px; }
  .router__image {
    display: none; }

  .footer-link {
    display: block;
    text-align: center; }

  .speedT-pt {
    display: block;
    padding-top: 20px; }

  .link_tp {
    padding-top: 20px; }

  .center_link {
    margin: 0 auto; }

  .footer__accordion-wrapper {
    display: block;
    padding-left: 10%; }

  .services-card__item {
    width: calc(100% - 20px); }

  .services-card {
    justify-content: center; }

  .info__title {
    font-size: 38px; }
  .HowConnect-content__title {
    font-size: 38px; }
  .rate-content__title {
    font-size: 38px; }
  .capability-content {
    margin-right: 0; }

  .capability-img {
    display: none; }

  .application-about {
    display: block; }

  .middle__title {
    font-size: 32px; }
  .about-text {
    margin-bottom: 50px; }
  .about-link {
    display: flex;
    justify-content: center;
    .btn__link {
      margin-left: 7px;
      & + .btn__link {
        margin-top: 0;
        margin-left: 18px; } } }

  .HowConnect-content__list {
    padding-left: 0px; }

  .HowConnect-feedback__title {
    font-size: 24px; }

  .HowConnect-feedback__text {
    font-size: 24px; }

  .feedback-numb {
    margin: 0 10px; } }
@media only screen and (max-width : 320px) {
  .mobile-content__link {
    font-size: 19px; } }
