@import "bourbon";
@import "fonts";
@import "vars";




* {

  font-family: $default_font; }
label {
  font-weight: inherit; }

body {
  //display: none
  font-size: 17px;
  color: white;
  line-height: 1;
  font-weight: 500; }

button {
  padding: 0;
  border: none;
  font: inherit;
  cursor: pointer;
  &:focus {
    outline: none; } }

input, select {
  border: none;
  margin: 0;
  outline: none; }


input[type=file]:focus,
input[type=checkbox]:focus,
input[type=radio]:focus {
  outline: none; }

input[type=checkbox],
input[type=radio] {
  margin: 0; }
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr,
acronym, address, big, cite, code, del, dfn, em, img, ins, kbd,
q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i,
center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td, article, aside,
canvas, details, embed, figure, figcaption, footer, header,
hgroup, menu, nav, output, ruby, section, summary, time, mark,
audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline; }

ul {
  margin: 0;
  padding: 0;
  list-style: none; }
a {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
  &:hover {
    text-decoration: none;
    color: inherit; }
  &:focus {
    outline: none;
    text-decoration: none;
    color: inherit; } }

header {
  background: #1F2431; }

.top-line {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #757575; }

.top-line__left {
  display: flex;
  align-items: center;
  color: #8D96B2; }


.top-line__left-lang {
  margin-right: 27px;
  font-weight: 700;
  padding-top: 8px; }

.textTransform {
  text-transform: uppercase; }

.dropdown-menu {
  left: -10px;
  margin-top: 10px;
  min-width: 75px;
  li {
    a {
      padding-left: 12px; } } }

.top-line__left-location {
  background: url("../img/icons/left-location.svg");
  width: 88px;
  height: 21px; }

.top-line__menu {
  margin: 0; }

.top-line__menu-item {
  display: inline-block;
  margin: 0 19px; }




.top-line__menu-link {
  position: relative;
  color: #fff;
  font-size: 17px;
  display: inline-block;
  padding: 24px 0;
  &:hover {
    background-image: $orangeToPink;
    -webkit-background-clip: text;
    color: transparent;
    transition: all .2s ease-in; }
  &:hover:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    background-image: $orangeToPink;
    width: 100%;
    height: 4px; } }


.tools__item {
  display: inline-block;
  vertical-align: middle;
  line-height: 25px; }

.top-line__tools-basket {
  margin-right: 20px;
  &:before {
    content: "";
    background: url("../img/icons/Bag.svg") no-repeat;
    background-size: auto 25px;
    display: inline-block;
    height: 26px;
    width: 30px;
    vertical-align: top; } }

.top-line__tools-personal {
  &:before {
    content: "";
    background: url("../img/icons/Profile.svg") no-repeat;
    background-size: auto 25px;
    display: inline-block;
    height: 26px;
    width: 26px;
    vertical-align: top; } }

// SWIPE RIGHT MENU START
.menu__bg {
  display: none;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 500;
  background-color: rgba(0, 0, 0, 0.3); }
.burger {
  display: none;
  cursor: pointer; }

.burger-item {
  width: 25px;
  height: 3px;
  background-color: #ffffff;
  margin: 5px; }

.right-menu__close {
  padding: 30px;
  position: absolute;
  top: 0; }

.right-menu {
  display: none;
  position: fixed;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 1000;
  width: 330px;
  background-color: #1F2431;
  transform: translateX(105%);
  transition: transform .5s ease-in;
  text-align: left;
  padding-top: 70px; }

.right-menu__wrap {
  height: 100%;
  overflow-y: auto;
  padding: 20px 30px 75px;
  position: relative; }

.right-menu__btn {
  width: 21px;
  height: 21px;
  background: none;
  border: none;
  background-image: url(/img/icons/CloseMenu.svg); }

.right-menu__close {
  text-align: left; }



.right-menu-contacts {
  display: none;
  padding: 55px 0 40px;
  text-align: left; }

.right-menu__category {
  font-weight: 500;
  font-size: 21px;
  line-height: 26px;
  margin-bottom: 36px; }

.category__item + .category__item {
  margin-top: 18px; }

.category__item {
  &:active {
    background-image: $orangeToPink;
    -webkit-background-clip: text;
    color: transparent; } }

.right-menu__tools {
  text-align: left;
  margin-bottom: 30px; }

.right-menu__tools-basket {
  display: block;
  margin-bottom: 29px;
  &:before {
    content: "";
    background: url("../img/icons/Bag.svg") no-repeat;
    background-size: auto 25px;
    display: inline-block;
    height: 26px;
    width: 30px;
    vertical-align: top; } }

.right-menu__tools-personal {
  display: block;
  &:before {
    content: "";
    background: url("../img/icons/Profile.svg") no-repeat;
    background-size: auto 25px;
    display: inline-block;
    height: 26px;
    width: 26px;
    vertical-align: top; } }

.right-line__link {
  display: flex;
  font-size: 17px;
  font-weight: 500;
  line-height: 21px;
  &:active {
    .right-line__link-text {
      color: #ffffff; }
    .right-line__icon {
      fill: white; }
    .right-line__icon-phone {
      stroke: #ffffff; } } }

.right-menu__list-item + .right-menu__list-item {
  margin-top: 25px; }

.right-line__icon {
  transition: all .2s ease-in;
  fill: $secondary_color;
  width: 20px;
  height: 21px;
  margin-right: 18px; }

.right-line__link-text {
  transition: all .2s ease-in;
  color: $secondary_color; }

.icon-phone {
  transition: all .2s ease-in;
  fill: none;
  stroke: $secondary_color; }

.icon-wi-fi {
  width: 21px;
  height: 27px; }




// SWIPE RIGHT MENU END

.middle-line {
  display: flex;
  justify-content: space-between;
  padding: 48px 15px 47px 15px;
  &__wrapper-left {
    width: 55%;
    display: flex;
    align-items: center; }
  &__wrapper-right {
    display: flex;
    align-items: center; } }

.middle-line__logo {
  margin-right: 15px;
  max-width: 254px;
  width: 100%;
  height: 46px; }

.middle-line__promotion {
  color: #ffffff;
  margin: 0;
  font-size: 21px;
  line-height: 25px;
  font-weight: 500;
  padding-left: 16px;
  position: relative;
  &:before {
    content: "";
    position: absolute;
    display: block;
    background: #ffffff;
    height: 100%;
    width: 2px;
    top: 0;
    left: 0; } }

.middle-line__email {
