    text-decoration: underline; } }

.middle-line__tel {
  font-size: 24px;
  font-weight: 700;
  line-height: 29px;
  background-image: $orangeToPink;
  -webkit-background-clip: text;
  color: transparent;
  margin-right: 18px;
  cursor: pointer;
  &:hover {
    color: transparent; } }

.telegMarg {
  padding-right: 3px; }

.viber__Left {
  margin-left: 15px; }

.bottom-line-fixed {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 5000;
  background-color: #1f2431;
  padding: 5px 0; }

.bottom-line__list {
  padding: 10px 0 11px;
  display: flex;
  justify-content: center; }

.bottom-line__link {
  display: inline-block;
  font-size: 14px;
  font-weight: 500;
  line-height: 17px;
  text-align: center;
  &:hover {
    .bottom-line__link-text {
      color: #ffffff; }
    .bottom-line__icon {
      fill: white; }
    .bottom-line__icon-phone {
      stroke: #ffffff; } } }

.bottom-line__list-item+.bottom-line__list-item {
  margin-left: 38px; }


.bottom-line__icon {
  transition: all .2s ease-in;
  fill: $secondary_color;
  width: 33px;
  height: 27px; }

.bottom-line__link-text {
  transition: all .2s ease-in;
  display: block;
  font-size: 14px;
  color: $secondary_color; }

.bottom-line__icon-phone {
  transition: all .2s ease-in;
  fill: none;
  stroke: $secondary_color;
  width: 24px;
  height: 25px; }

// SLIDER START

.promotion-slider__item {
  position: relative;
  min-height: 600px;
  width: 195px;


  //&:before
  //  content: ''
  //  width: 100%
  //  height: 100%
  //  display: block
  //  position: absolute
  //  top: 0
